.app-entry {
	height: 100%;

	max-width: 900px;
	min-height: 400px;

	margin: 0 auto;

	-webkit-box-sizing: border-box;

	        box-sizing: border-box;

	position: relative;
}

.app-entry__content {
	position: absolute;
	left: 0;
	top: 10px;
	right: 0;
	bottom: 10px;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;

	overflow: hidden;
}

.app-entry__conversations {
	width: 300px;
	height: 100%;

	background: #ff7aab;
	-ms-flex: 0 0 auto;
	    flex: 0 0 auto;
	overflow: hidden;
}

.app-entry__current-conversation {
	-ms-flex: 1 1 auto;
	    flex: 1 1 auto;

	height: 100%;

	background: #eee;
}



